@import 'variables.scss';
body {
  font-family: $ff; }

.container {
  max-width: 1440px;
  margin: 0 auto; }

// Header
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  .navigation__list {
    display: flex;
    list-style-type: none;
    align-items: center;
    .navigation__item {
      margin: 0 .5rem;
      .navigation__link {
        color: inherit;
        text-decoration: none; } } } }
